import React, { Component } from 'react';
import RenderLoginBox from './RenderLoginBox';
import { Link } from 'react-router-dom';
import * as images from '../../images';

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoggingIn: false
    }
  }

  switchBox(event) {
    event.preventDefault();
    this.setState({
      isLoggingIn: !this.state.isLoggingIn
    })
  }

  handleLogin(e) {
    e.preventDefault();
  }

  render() {
    const { isLoggingIn } = this.state;

    return (
      <div>
        <div className="container-fluid">
          <div className="header-home">
            <video autoPlay loop className="header-home-video">
              <source src={images.videoMP4} type="video/mp4" />
              <source src={images.videoWebm} type="video/webm" />
              <source src={images.videoOgv} type="video/ogg" />
            </video>
            <div className="header-home-top">
              <div className="row">
                <div className="offset-by-four four columns header-home-top-logo">
                  <img src={images.logoBlanc} alt="WorkEat" className="header-home-top-logo-item" />
                </div>
                <div className="four columns">
                  <p className="header-home-top-connexion">Dj inscrit ? <a href="#" className="header-home-top-connexion-link" onClick={this.switchBox.bind(this)}>Connexion</a></p>
                </div>
              </div>
              <div className="row">
                <div className="twelve columns">
                  <h2 className="header-home-top-subtitle">La gastronomie livre</h2>
                </div>
              </div>
            </div>
            <div className="header-home-form">
              <div className="row">
                <div className="twelve columns">
                  <RenderLoginBox isLoggingIn={isLoggingIn}/>
                </div>
              </div>
            </div>
          </div>
          <div className="instructions">
            <div className="instructions-container">
              <div className="instructions-step">
                <img className="instructions-step-icon" src={images.checkForm} alt="Formulaire d'inscription" />
                <h3 className="instructions-step-text">Inscrivez-vous<br />sur le site</h3>
              </div>
              <div className="instructions-step">
                <picture>
                  <source media="(max-width:550px)" srcSet={images.angleDown} />
                  <img className="instructions-step-icon" src={images.angle} alt="Suivant" />
                </picture>
              </div>
              <div className="instructions-step">
                <img className="instructions-step-icon" src={images.touch} alt="Choix des produits" />
                <h3 className="instructions-step-text">Commandez<br /> votre menu</h3>
              </div>
              <div className="instructions-step">
                <picture>
                  <source media="(max-width:550px)" srcSet={images.angleDown} />
                  <img className="instructions-step-icon" src={images.angle} alt="Suivant" />
                </picture>
              </div>
              <div className="instructions-step">
                <img className="instructions-step-icon" src={images.truck} alt="Livraison de la commande" />
                <h3 className="instructions-step-text">Livr prs de<br /> votre entreprise</h3>
              </div>
            </div>
          </div>
        </div>
        <div className="container-fluid">
          <div className="concept">
            <div className="concept-container">
              <h1 className="concept-title">Le concept</h1>
              <hr className="divider" />
              <p className="concept-content">Nous proposons diffrentes entres, plats, desserts et boissons afin de satisfaire vos papilles. Apres avoir command ce que qu'il vous plait, nous vous livrons dans un rayon de cinq kilomtres autour de notre entrepot.</p>
              <Link to="/about" className="btn-gold">A propos</Link>
            </div>
            <div className="concept-container">
              <img src={images.concept} alt="Concept" className="concept-images" />
            </div>
          </div>
        </div>
        <div className="container-fluid menu-bg">
          <div className="menu">
            <div className="menu-container">
              <div className="menu-container-row">
                <img className="menu-container-row-images" src={images.brochette} alt="brochette de viande" />
                <img className="menu-container-row-images" src={images.sushi} alt="Nos Sushis" />
              </div>
              <div className="menu-container-row">
                <img className="menu-container-row-images" src={images.burger} alt="nos burgers" />
                <img className="menu-container-row-images" src={images.crevette} alt="Nos crevettes" />
              </div>
            </div>
            <div className="menu-container">
              <h1 className="menu-container-title">Une semaine, un menu</h1>
              <hr className="divider" />
              <p className="menu-container-text">Chaque semaine nous changeons notre carte pour vous apporter toujours plus de plats diffrents</p>
              <button className="btn-gold">Voir le menu</button>
            </div>
          </div>
        </div>
        <div className="container-fluid">
          <div className="livraison">
            <div className="livraison-container">
              <h1 className="livraison-title">Livr prs de votre travail</h1>
              <hr className="divider" />
              <p>Nous livrons dans quatre points diffrents, tous au maximum  5km. Vous pourrez choisir votre point de livraison favori et ainsi venir rcuprer votre commande juste aprs la livraison.</p>
              <button className="btn-gold">Commander</button>
            </div>
            <div className="livraison-container">
              <img src={images.livraison} alt="Image Livraison" className="livraison-images" />
            </div>
          </div>
        </div>
        <div className="container-fluid">
          <div className="footer">
            <div className="footer-logo">
              <img className="footer-logo-item" src={images.logoBlanc} alt="Logo WorkEat" />
            </div>
            <div className="footer-list">
              <ul className="footer-list-social">
                <li className="footer-list-social-item"><a href="#" className="footer-list-social-item-link"><i className="icon-facebook-square footer-list-social-item-link-icon"></i></a></li>
                <li className="footer-list-social-item"><a href="#" className="footer-list-social-item-link"><i className="icon-twitter-square footer-list-social-item-link-icon"></i></a></li>
                <li className="footer-list-social-item"><a href="#" className="footer-list-social-item-link"><i className="icon-pinterest-square footer-list-social-item-link-icon"></i></a></li>
                <li className="footer-list-social-item"><a href="#" className="footer-list-social-item-link"><i className="icon-instagrem footer-list-social-item-link-icon"></i></a></li>
              </ul>
              <ul className="footer-list-mentions">
                <li className="footer-list-mentions-item"><a href="#" className="footer-list-mentions-item-link">Mentions lgales</a></li>
                <li className="footer-list-mentions-item"></li>
                <li className="footer-list-mentions-item"><a href="#" className="footer-list-mentions-item-link">CGV</a></li>
                <li className="footer-list-mentions-item"></li>
                <li className="footer-list-mentions-item"><a href="#" className="footer-list-mentions-item-link">Plan du site</a></li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;




